#intro{
    height:81vh;
    width: 100vw;
    overflow: hidden;
    background-image: url(../../assets/bg.jpg);
    background-repeat:no-repeat;
    background-size: cover;
}
.bg{
    position: absolute;
    top:5rem;
    left: 0;
    display: flex;
    object-fit: cover;
    height:78.3vh;
}
.introContent{
    top: 0;
    margin-left:32rem;
    height:50vh;
    width:95vw;
    padding:2rem;
    font-size:3.5rem;
    font-weight:800;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.boxpara{
    font-size:1.2rem;
    height: 3rem;
    top:0;
    display: flex;
    margin-left:33rem;
    align-items: center;
    justify-content: center;
    text-align: center;
    width:40vw;
    
}
.btn{
    margin:2rem;
    margin-left:40rem;
    font-weight:800;
    font-size:large;
    height:10vh;
    width:23vw;
    cursor: pointer;
    background:#FEE12B;
    padding: 0.75rem 1.5rem;
    border: none;
    border-radius:3rem;
}

.color-change {
    font-size: 3.5rem;
    color: transparent;
    animation: gradientAnimation 4s infinite ;
    -webkit-background-clip: text;
    background-clip: text;
}
.text-animation {
    animation: changeText 4s infinite;
}

@keyframes changeText {
    0%{
        content: "Money...";
    }
    25% {
        content: "Revenu...";
    }
    50% {
        content: "Customers...";
    }
    75% {
        content: "Sales...";
    }
}

@keyframes gradientAnimation {
    0% {
        background-image: linear-gradient(to left, #f15ac4, #f53051);
    }
    100% {
        background-image: linear-gradient(to right, #30e0ec, #3f5feb);
    }
}

/* Media query for smaller screens */
@media screen and (max-width:320px) {
    #intro {
        height: auto; /* Adjust height as needed */
    }

    .bg {
        top:4rem; /* Adjust top position */
        height:42vh; /* Adjust height as needed */
    }

    .introContent {
        align-items: center;
        justify-content: center;
        margin-top:8rem;
        margin-left:-2rem;
        font-size:14px; /* Adjust font size as needed */
    }
    .color-change{
        font-size: 15px;
    }

    .boxpara {
        margin-top:-6rem;
        margin-left:2rem; /* Adjust margin as needed */
        font-size:9px; /* Adjust font size as needed */
    }

    .btn {
        margin-left:1rem; /* Adjust margin as needed */
        font-size:9px; /* Adjust font size as needed */
        width:50vw;
        height:10vh;
    }
}