#marketings{
    height:130vh;
    width: 100vw;
    overflow: hidden;
    background-image: url(../../assets/bg.jpg);
    background-repeat:no-repeat;
    background-size:cover;
}
.marketingTitle{
    margin:3rem;
    font-size:2rem;
    font-weight:900;
    text-align: center;
    align-items: center;
    flex-direction:column;
    justify-content: center;
}
#mainBlock{
    margin-left:4rem;
    display: flex;
    flex-wrap: wrap;
}
.serviceBlocks{
    padding:1rem;
    width:28vw;
    text-align:left;
}
.serviceBlock{
    display: flex;
    margin: 0.2rem;
    padding: 1rem 1rem;
    border-radius:6rem;
    background:rgb(40, 40, 40);
    opacity:75%;
}
.serviceBlockImg{
    margin-top:0.4rem;
    object-fit: cover;
    height: 4rem;
    width: 4rem;
    margin-left:1rem;
}
.serviceBlockText{
    margin-left:1rem;
    padding:1rem;
}

/* Media query for mobile devices */
@media (max-width:320px) {
    #marketings {
        top: 0;
        height: auto;
    }

    .marketingTitle {
        margin-top:1rem;
        margin-left:0rem;
        width: 60vw;
        font-size:15px; /* Adjust font size for smaller screens */
    }
    #mainBlock {
        margin-left: 0;
    }

    .serviceBlocks {
        height:15vh;
        width:60vw;
    }

    .serviceBlock {
        margin-top: -2rem;
        padding: 0rem;
        border-radius: 1rem;
    }

    .serviceBlockImg {
        margin-top:0.8rem;
        height:1.6rem;
        width: 1.6rem;
        margin-left:0.5rem;
    }

    .serviceBlockText {
        font-size: 8px;
        margin-left:0;
    }
}