#slider{
    left: 0;
    height:15vh;
    background-color: white;
    overflow: hidden;
    width:100%;
    max-width:100rem;
    display: flex;
    flex-direction:row;
    align-items: center;
    justify-content: center;
    text-align: center;
    z-index: -1;
}
.boxslider{
    width:100%;
    margin-left:3rem;
    display: flex;
    max-width:100rem;
}
.sliderImg{
  padding: 0 2rem;
    margin:1rem;
    display: inline-block;
    line-height: 100px;
}
.sliderTitle{
  text-align: center;
    margin-left:4rem;
    color: black;
    width:10rem;
}

.marquee {
  white-space: nowrap; /* Prevent line breaks */
  overflow: hidden; /* Hide overflowing content */
  animation: marquee 1s linear infinite; /* Adjust duration as needed */
}

.marquee img {
  margin-right: 20px; /* Adjust spacing between images */
}

@keyframes marquee {
  0% {
      transform: translateX(100%); /* Start off-screen to the right */
  }
  100% {
      transform: translateX(-100%); /* Move off-screen to the left */
  }
}

/* Responsive styles for mobile devices */
@media (max-width: 320px) {
  #slider {
      height: auto;
      flex-direction: column; /* Stack items vertically */
      padding:5px;
  }

  .sliderImg {
    height:20px;
      padding: 0;
      margin: 0;
  }

  .sliderTitle {
    margin-left:-7.5rem;
    font-size: 14px;
  }
}